#page-landing{
    background: linear-gradient(329.54deg, #29b6d1 0%, #00c7c7 100%);
width: 100vw;
height: 100vh;
display: flex;
justify-content:center;
align-items:center;
}

#page-landing .content-wrapper {
    position:relative;
    width: 100%;
    max-width:1100px;

    height:100%;
    max-height:680px;

    display:flex;
    align-items:flex-start;
    flex-direction:column;
    justify-content:space-between;

    background: url('../../Images/Landing.svg') no-repeat 80% center;
}

#page-landing .content-wrapper main { max-width: 358px; }


#page-landing .content-wrapper main h1{
    /* max-width:350px; */
    font-size: 76px;
    font-weight: 900;
    line-height:70px;
}

#page-landing .content-wrapper main p{
   /* max-width:350px; */
    margin-top: 20px;
    font-size:24px;
    line-height:34px;

}


.content-wrapper .location{
    position:absolute;
    right:0;
    top:0;

    font-size:24px;
    line-height:34px;

    display:flex;
    flex-direction: column;
    text-align: right;
}

.content-wrapper .location strong {
    font-weight: 800;
}

.content-wrapper .enter-app{
    position:absolute;
    right:0;
    bottom:0;

    width:80px;
    height:80px;
    background: #ffd666;
    border-radius: 30px;

    display:flex;align-items:center;

    justify-content:center;
    transition: bakcground-color 0.3s;
}

.content-wrapper .enter-app:hover{
    background:#96feff;
}